import React, { useState, useEffect, useRef } from "react";
import { useParams, Link } from "react-router-dom";
import Header from "../common/Header";
import Nav from "../common/Nav";
import axiosInstance from "../../api/axiosInstance";
import SockJS from "sockjs-client";
import { Client } from "@stomp/stompjs";
import "../../styles/ChatRoom.css";
import { parseTracks } from "../../utils/trackUtils";
import Music from "../music/Music";
import { getDefaultImage } from "../../utils/imageUtils";
import SidebarLayout from "../common/SidebarLayout";

const ChatRoom = () => {
    const { streamId } = useParams();
    const [messages, setMessages] = useState([]);
    const [newMessage, setNewMessage] = useState("");
    const [user, setUser] = useState(null);
    const [stream, setStream] = useState(null);
    const [client, setClient] = useState(null);
    const messagesEndRef = useRef(null); // ë©”ì‹œì§€ ë ì°¸ì¡°
    const [playlist, setPlaylist] = useState(null); // í”Œë ˆì´ë¦¬ìŠ¤íŠ¸ ìƒíƒœ ì¶”ê°€
    const [isOpen, setIsOpen] = useState(false); // ìƒíƒœ ì¶”ê°€

    const toggleStreamingTracks = () => {
        setIsOpen(!isOpen); // ìƒíƒœ í† ê¸€
    };

    useEffect(() => {
        const fetchStream = async () => {
            try {
                const response = await axiosInstance.get(`/api/streaming/${streamId}`);
                setStream(response.data);
            } catch (error) {
                console.error("ìŠ¤íŠ¸ë¦¬ë° ì •ë³´ë¥¼ ê°€ì ¸ì˜¤ëŠ” ë° ì‹¤íŒ¨:", error);
            }
        };

        // ë¡œê·¸ì¸í•œ ìœ ì € ì •ë³´ ê°€ì ¸ì˜¤ê¸°
        const fetchUser = async () => {
            try {
                const response = await axiosInstance.get("/api/spotify/userInfo");
                setUser(response.data);
            } catch (error) {
                console.error("ìœ ì € ì •ë³´ë¥¼ ê°€ì ¸ì˜¤ëŠ” ë° ì‹¤íŒ¨:", error);
            }
        };

        fetchStream();
        fetchUser();
        
        // SockJSë¥¼ ì‚¬ìš©í•˜ì—¬ WebSocket ì„¤ì •
        const socket = new SockJS(`${process.env.REACT_APP_API_URL}/chat`);
        const stompClient = new Client({
            webSocketFactory: () => socket,
            connectHeaders: {
                login: "user",
                passcode: "password",
            },
            onConnect: () => {
                console.log("STOMP ì—°ê²° ì„±ê³µ");
                stompClient.subscribe(`/topic/chat/${streamId}`, (message) => {
                    const newMessage = JSON.parse(message.body);
                    setMessages((prevMessages) => [...prevMessages, newMessage]);
                });
            },
            onStompError: (frame) => {
                console.error("STOMP ì˜¤ë¥˜:", frame.headers["message"]);
            },
            onWebSocketError: (error) => {
                console.error("WebSocket ì˜¤ë¥˜:", error);
            },
            debug: (str) => {
                console.log(str);
            },
        });

        setClient(stompClient);
        stompClient.activate();

        return () => {
            stompClient.deactivate();
        };
    }, [streamId]);

    useEffect(() => {
        const fetchPlaylist = async () => {
            if (stream && stream.playlist) {
                try {
                    const response = await axiosInstance.get(`/playlist/detail/${stream.playlist.playlistId}`);
                    const trackList = parseTracks(response.data.tracks);
                    setPlaylist({
                        ...response.data.playlist,
                        tracksData: trackList,
                        playlistPhoto: response.data.playlist.playlistPhoto || getDefaultImage(), // ê¸°ë³¸ ì´ë¯¸ì§€ ì„¤ì •
                    });
                } catch (error) {
                    console.error("Error fetching playlist detail", error);
                }
            }
        };

        fetchPlaylist();
    }, [stream]); // streamì´ ë³€ê²½ë  ë•Œë§ˆë‹¤ í˜¸ì¶œ

    // ë©”ì‹œì§€ê°€ ì—…ë°ì´íŠ¸ë  ë•Œë§ˆë‹¤ ìŠ¤í¬ë¡¤ì„ ì•„ë˜ë¡œ ë‚´ë¦¬ëŠ” íš¨ê³¼
    useEffect(() => {
        if (messagesEndRef.current) {
            messagesEndRef.current.scrollIntoView({ behavior: "smooth" });
        }
    }, [messages]); // messages ë°°ì—´ì´ ë³€ê²½ë  ë•Œë§ˆë‹¤ ì‹¤í–‰

    const sendMessage = () => {
        if (!newMessage.trim()) return;

        const messageData = {
            streamId,
            sender: user ? user.display_name : "Unknown User",
            content: newMessage,
            receiver: "receiver_nickname",
        };

        if (client && client.connected) {
            client.publish({
                destination: `/app/chat/${streamId}`,
                body: JSON.stringify(messageData),
            });
            setNewMessage("");

            // ë§ˆì§€ë§‰ ë©”ì‹œì§€ë¥¼ ìŠ¤íŠ¸ë¦¬ë°ì˜ ë§ˆì§€ë§‰ ì±„íŒ…ìœ¼ë¡œ ì—…ë°ì´íŠ¸í•˜ëŠ” API í˜¸ì¶œ (ì¶”ê°€)
            axiosInstance.post(`/api/streaming/${streamId}/lastMessage`, {
                lastMessage: messageData.content,
            })
            .catch(error => console.error("ë§ˆì§€ë§‰ ë©”ì‹œì§€ë¥¼ ì €ì¥í•˜ëŠ” ë° ì‹¤íŒ¨:", error));
        } else {
            console.error("STOMP í´ë¼ì´ì–¸íŠ¸ê°€ ì—°ê²°ë˜ì–´ ìˆì§€ ì•ŠìŠµë‹ˆë‹¤.");
        }
    };

    const handleKeyDown = (e) => {
        if (e.key === "Enter") {
            sendMessage();
        }
    };

    return (
        <div className="chat-container">
            <Header />

            <SidebarLayout>
            <div className="social-layout">

                <div>
                    <div className="chat-wrapper">
                        <div className="exit">
                            <h2 className="chat-title">
                                {stream ? `${stream.hostUser?.nickname}ì˜ ìŠ¤íŠ¸ë¦¬ë°` : "ë¡œë”© ì¤‘..."}
                                <Link to="/social"><button>ë‚˜ê°€ê¸°</button></Link>
                            </h2>
                        </div>

                        {playlist ? ( // playlist ìƒíƒœ ì‚¬ìš©
                            <div className="streaming-playlist-info">
                                <div className="playlist-info-place">
                                    <p id="explain"><strong>ì„¤ëª…:</strong> {playlist.playlistComment || "ì„¤ëª…ì´ ì—†ìŠµë‹ˆë‹¤."}</p>
                                    <p><strong>íŠ¸ë™ ìˆ˜:</strong> {playlist.tracksData ? playlist.tracksData.length : 0}ê³¡</p>
                                    <button onClick={toggleStreamingTracks}>
                                        {isOpen ? 'ì ‘ê¸°' : 'í¼ì³ë³´ê¸°'} {/* ë²„íŠ¼ í…ìŠ¤íŠ¸ ë³€ê²½ */}
                                    </button>
                                    </div> 
                                        {isOpen && ( // ì¡°ê±´ë¶€ ë Œë”ë§
                                        <div className="content-nav2">
                                            <div className="streaming-tracks">
                                                {playlist.tracksData.map((track, index) => (
                                                    <Music key={index} track={track} />
                                                ))}
                                            </div>
                                            
                                        </div>
                                       
                                    )}
                                </div>
                        ) : (
                            <p className="playlist-info-place">í”Œë ˆì´ë¦¬ìŠ¤íŠ¸ ì •ë³´ê°€ ì—†ìŠµë‹ˆë‹¤.</p>
                        )}
                        <div className="chat-messages-input-wrapper">
                            <div className="chat-messages">
                                {messages.length > 0 ? (
                                    messages.map((msg, index) => (
                                        <div
                                            key={index}
                                            className={`chat-message ${msg.sender === (user ? user.display_name : "Unknown User") ? 'my-message' : 'other-message'}`}
                                        >
                                            <strong>{msg.sender}:</strong> <span>{msg.content}</span>
                                        </div>
                                    ))
                                ) : (
                                    <div className="no-messages">ë©”ì‹œì§€ê°€ ì—†ìŠµë‹ˆë‹¤.</div>
                                )}
                                <div ref={messagesEndRef} /> {/* ë©”ì‹œì§€ ë ì°¸ì¡° */}
                            </div>
                            <div className="chat-input">
                                <input
                                    type="text"
                                    value={newMessage}
                                    onChange={(e) => setNewMessage(e.target.value)}
                                    onKeyDown={handleKeyDown}
                                    placeholder="ë©”ì‹œì§€ë¥¼ ì…ë ¥í•˜ì„¸ìš”..."
                                />
                                <button onClick={sendMessage}>ì „ì†¡</button>
                            </div>
                        </div>
                    </div>
                </div>
                <Nav />
            </div>
            </SidebarLayout>
        </div>
    );
};

export default ChatRoom;
